"use client";

import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { UserCircleIcon, CameraIcon } from "@heroicons/react/24/solid";

// âœ… Define the Type for a Ride
type Ride = {
  title: string;
  from: string;
  to: string;
  time: string;
  maxPassengers: number;
  price: number;
  description: string;
};

export default function OfferRide() {
  const [title, setTitle] = useState("");
  const [from, setFrom] = useState("");
  const [to, setTo] = useState("");
  const [time, setTime] = useState("");
  const [maxPassengers, setMaxPassengers] = useState("");
  const [price, setPrice] = useState("");
  const [description, setDescription] = useState("");
  const [rides, setRides] = useState<Ride[]>([]); // âœ… Now TypeScript knows the structure

  // Load existing rides from localStorage
  useEffect(() => {
    const storedRides: Ride[] = JSON.parse(localStorage.getItem("rides") || "[]");
    setRides(storedRides);
  }, []);

  const postRide = () => {
    if (title.trim() && from.trim() && to.trim() && time.trim() && maxPassengers.trim() && price.trim()) {
      const newRide: Ride = {
        title,
        from,
        to,
        time,
        maxPassengers: parseInt(maxPassengers, 10),
        price: parseFloat(price),
        description,
      };

      const updatedRides = [...rides, newRide];
      setRides(updatedRides);
      localStorage.setItem("rides", JSON.stringify(updatedRides));

      setTitle("");
      setFrom("");
      setTo("");
      setTime("");
      setMaxPassengers("");
      setPrice("");
      setDescription("");
    } else {
      alert("Please fill in all fields.");
    }
  };

  return (
    <div className="p-6 max-w-xl mx-auto">
      <h1 className="text-3xl font-bold mb-4 text-center">ðŸš— Offer a Ride</h1>

      {/* Ride Form */}
      <Input placeholder="Title your post" value={title} onChange={(e) => setTitle(e.target.value)} className="mt-2 border rounded-lg p-3" />
      <Input placeholder="From" value={from} onChange={(e) => setFrom(e.target.value)} className="mt-2 border rounded-lg p-3" />
      <Input placeholder="To" value={to} onChange={(e) => setTo(e.target.value)} className="mt-2 border rounded-lg p-3" />
      <Input type="datetime-local" value={time} onChange={(e) => setTime(e.target.value)} className="mt-2 border rounded-lg p-3" />
      <Input placeholder="Max Passengers" type="number" value={maxPassengers} onChange={(e) => setMaxPassengers(e.target.value)} className="mt-2 border rounded-lg p-3" />
      <Input placeholder="Fixed Price" type="number" value={price} onChange={(e) => setPrice(e.target.value)} className="mt-2 border rounded-lg p-3" />
      <textarea placeholder="Description (e.g., 'No smoking, only 2 bags allowed')" value={description} onChange={(e) => setDescription(e.target.value)} className="w-full mt-2 p-3 border rounded-lg"></textarea>

      <div className="flex justify-between mt-4">
        <Button className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded-lg" onClick={() => window.history.back()}>
          Go Back
        </Button>
        <Button className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg" onClick={postRide}>
          Post Ride
        </Button>
      </div>

      {/* Display Created Rides */}
      <h2 className="text-2xl font-bold mt-6">Your Rides</h2>
      <div className="mt-4 space-y-4">
        {rides.length === 0 ? (
          <p className="text-gray-500 text-center">No rides available.</p>
        ) : (
          rides.map((ride, index) => (
            <div key={index} className="border p-4 rounded-lg shadow-lg bg-white flex items-start gap-4">
              {/* Profile Icon */}
              <div className="flex flex-col items-center">
                <UserCircleIcon className="w-12 h-12 text-gray-600" />
              </div>

              {/* Ride Details */}
              <div className="flex-1">
                <h3 className="text-lg font-bold">{ride.title}</h3>
                <p className="text-gray-600">{ride.description}</p>
                <div className="mt-2 text-sm">
                  <strong>From:</strong> {ride.from} â†’ <strong>To:</strong> {ride.to}
                  <br />
                  <strong>Time:</strong> {new Date(ride.time).toLocaleString("en-US", {
                    month: "long",
                    day: "numeric",
                    year: "numeric",
                    hour: "2-digit",
                    minute: "2-digit",
                    hour12: true,
                  })}
                  <br />
                  <strong>Seats:</strong> {ride.maxPassengers} | <strong>Price:</strong> ${ride.price}
                </div>
              </div>

              {/* Add an Icon for a Ride Image (Placeholder) */}
             
            </div>
          ))
        )}
      </div>
    </div>
  );
}
